import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
import ds from "@/components/dashboard";
import blog from "@/components/blog";
import child from "@/components/child";
import covtr from "@/components/covidtracking"
import vueprjk  from "@/components/vueproject";
import logn from '@/components/login_sys';
const routes =  [
   
    {
        path:'/',
        component:ds,
    },
    {
        path:'/covid_',
        component:covtr
    },
    {
        path:'/blog',
        component:blog,
        children:[
            {
                path:'/chil',
                component:child,
            }
        ]
    },
    {
        path:'/vue-project',
        component:vueprjk
    },
    {
        path:'/login_',
        component:logn
    }
    
]
export default new VueRouter({
   routes
});