<template>
    <div>
    <div>
     
      <nav class="navbar-b importan">
          <ul>
              <li><a href="index.html"><router-link to="/">Coronavirus Invormation</router-link></a></li>
          </ul>
      </nav>

       <div class="def"></div>
       <router-view></router-view>
       
      
       <div>
     
           <div class=" mg container">
              <div cslass="dn">
                        <select style="text-align:center" name="LeaveType" @change="onChange($event)" class="form-control" v-model="k">
   <option selected value="ID" >Indonesia</option>
   <option value="MY">Malaysia</option>
    <option value="JP">japan</option>
    <option value="US">USA</option>
    <option value="CN">China</option>
     <option value="IT">Italy</option>
      <option value="SE">Sweden</option>
       <option value="ES">Spanyol</option>
        <option value="AU">Australia</option>
</select>

              </div>
              <div class="dn">
         <div class="m"  v-for="item in da" v-bind:key="item.id" >
             <tr></tr>
         <p> Negara : {{item.country}}</p>
          <p>Confrimed : {{item.latest.confirmed}}</p>
          <p>Kematian : {{item.latest.deaths}}</p>
          <p>Dipulihkan : {{item.latest.recovered}}</p>
          <p>Provinsi : {{item.province}}</p>
          <br>
          </div>
       </div>
       <div class="dn">
           <p>Total seluruh dunia</p>
          <p>Confrimed :{{a}}</p>
          <p>Kematian : {{b}}</p>
          <p>Dipulihkan :{{c}}</p>
          </div>
       
    </div>
        </div>
    </div> 
    <div v-show="anim" class="overlay-load">
            <center>
                <img  src="../assets/hu.gif" width="140px" alt="">
                <h3>Loading. . . . .  </h3>
               
            </center>
        </div>
    <div id="n" class="overlay-show">
        
    <ul>
      <li><a href="#">About Me</a></li>
