</template>
<script>
import __AxIoS from 'axios';
 var uri ='https://coronavirus-tracker-api.herokuapp.com/v2/locations?country_code=ID';
 var Uri_Total ='https://coronavirus-tracker-api.herokuapp.com/v2/latest'
export default {
   
    data:()=>{
        return{
            msg:'sdasdsad',
            da:'',
            all:'',
            a:'',
            b:'',
            c:'',
            k:'',
            anim:'',
        }
    },
    mounted() {
        var this__ =this;
        this__.anim=true;
        __AxIoS.get(`${uri}`).then((resp)=>{
            this__.da=resp.data.locations;
            this__.anim=false;
        }).catch((err)=>{
            alert(err);
        });
        __AxIoS.get(`${Uri_Total}`).then((resp)=>{
            this__.all=resp.data;
            this__.a=resp.data.latest.confirmed;
            this__.b=resp.data.latest.deaths;
            this__.c=resp.data.latest.recovered;
            this__.anim=false;
       }).catch((err)=>{
            alert(err);
        })
    },
    methods: {
        onChange(event) {
            var this__=this;
            this__.anim=true;
             __AxIoS.get(`https://coronavirus-tracker-api.herokuapp.com/v2/locations?country_code=${event.target.value}`).then((resp)=>{
            this__.da=resp.data.locations;
            this__.anim=false;
        }).catch((err)=>{
            alert(err);
            this__.anim=true;
        });
            console.log(event.target.value)
        }
    }
}
</script>